@import "./app.scss";

.slider {
    background: transparent;
    height: fit-content;
    overflow: hidden;
    position: relative;
    width: 100vw;
    margin-bottom: 60px;

    &::after {
        right: 0;
        top: 0;

        transform: rotateZ(180deg);
    }

    &::before {
        left: 0;
        top: 0;
    }

    // Para futuros efectos de gradientes
