    // }

    .slide-track {
        display: flex;
        gap: 12px;
        // animation: scroll 50s linear infinite;
        // -webkit-animation: scroll 50s linear infinite;

        .slide {
            margin: 0;
            border-radius: 10px;
            text-align-last: center;
            width: 400px;

            @include mobile {
                width: 180px;
            }
        }
    }

